<script>
import { mapState } from "vuex";
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import HomeHeader from "@/components/HomeHeader.vue";
import HomeList from "@/components/HomeList.vue";

export default {
  name: "Home",
  components: {
    // HelloWorld
    HomeHeader,
    HomeList,
  },
  data() {
    return {
      gender: "male",
      bannerMale: [],
      bannerFemale: [],
      maleHot: null,
      maleFinish: null,
      maleOkami: null,
      femaleBestsell: null,
      femaleMasterpiece: null,
      femaleNice: null,
    };
  },
  computed: {
    ...mapState(["rankCategory"]),
  },
  created() {
    this.axios.get("./banner.json").then((response) => {
      this.bannerMale = response.data.male;
      this.bannerFemale = response.data.female;
      console.log(response);
      console.log(this.$route.query.gender);
    });

    this.axios.get("https://novel.kele8.cn/rank-category").then((response) => {
      console.log(response);
    });

    this.axios
      .get("https://novel.kele8.cn/rank/54d42d92321052167dfb75e3")
      .then((response) => {
        this.maleHot = response.data.ranking;
        console.log(response);
      });

    this.axios
      .get("https://novel.kele8.cn/rank/564eb878efe5b8e745508fde")
      .then((response) => {
        this.maleFinish = response.data.ranking;
      });
    // 男生大神榜 = 本周潜力榜
    this.axios
      .get("https://novel.kele8.cn/rank/54d42e72d9de23382e6877fb")
      .then((response) => {
        this.maleOkami = response.data.ranking;
      });
    // 女生畅销=追书最热榜54d43437d47d13ff21cad58b
    this.axios
      .get("https://novel.kele8.cn/rank/54d43437d47d13ff21cad58b")
      .then((response) => {
        this.femaleBestsell = response.data.ranking;
      });
    // 女生佳作=好评榜5a684551fc84c2b8efaab179
    this.axios
      .get("https://novel.kele8.cn/rank/5a684551fc84c2b8efaab179")
      .then((response) => {
        this.femaleMasterpiece = response.data.ranking;
      });
    // 女生红文区=热搜榜5a684515fc84c2b8efaa9875
    this.axios
      .get("https://novel.kele8.cn/rank/5a684515fc84c2b8efaa9875")
      .then((response) => {
        this.femaleNice = response.data.ranking;
      });
  },
  beforeRouteEnter(to, from, next) {
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
    next((vm) => {
      // 通过 `vm` 访问组件实例
      vm.gender = to.query.gender || "male";
    });
  },
  beforeRouteUpdate(to, from, next) {
    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
    // console.log(to, from);
    this.gender = to.query.gender || "male";
    next();
  },
};
</script>
